import jwx from '../../common/weiPay/jws.js'
export default {
	data() {
		return {
			mainItem: []
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {
		this.getList();
	},
	methods: {
		getList() {
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			_this.$request.card
				.gradeList({})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 1) {
						_this.mainItem = data.data;
						if (_this.mainItem.end_time == 0) {
							_this.mainItem.strDate = "未购买";
						} else {
							if (_this.mainItem.end_time * 1000 > Date.parse(new Date())) {
								_this.mainItem.strDate = _this.getDate(_this.mainItem.end_time * 1000) + " 过期";
							} else {
								_this.mainItem.strDate = "已过期";
							}
						}
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		getDate(item) { // 时间戳转日期
			var time = new Date(item)
			var year = time.getFullYear()
			var month = time.getMonth() + 1
			var date = time.getDate()
			return year + '-' + this.add0(month) + '-' + this.add0(date)
		},
		add0(m) {
			return m < 10 ? '0' + m : m
		},
		buy() {
			//购买入场券

			var _this = this;
			uni.showModal({
				title: '提示',
				content: '是否确认购买次入场券？',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据提交中'
						});
						_this.$request.common
							.paySundries({
								type: 1,
								grade_id: _this.mainItem.id
							})
							.then(data => {
								_this.$tools.loadingHide();
								if (data.status == 1) {
									_this.pay(data.msg);
								} else {
									_this.$tools.toast(data.msg);
								}
							})
							.catch(err => {
								console.log(err)
								_this.$tools.loadingHide();
								//消息异常
								_this.$tools.toast('数据加载异常');
							});
					} else if (res.cancel) {}
				}
			});
		},
		pay(orderInfo) {
			let _this = this;
			jwx({
				nonceStr: orderInfo.nonceStr,
				timeStamp: orderInfo.timeStamp,
				package: orderInfo.package,
				signType: orderInfo.signType,
				paySign: orderInfo.paySign
			}, res => {
				_this.$tools.toast('支付成功')
				setTimeout(() => {
					_this.getList();
				}, 1000)
			}, fail => {
				console.log(fail)
				_this.$tools.toast('支付失败')
			})
		}
	}
};
